import { defineComponent, toRefs } from "vue";
import { reactive } from "@vue/reactivity";
export default defineComponent({
  name: "BackBtn",
  emits: ["backClickedEmit"],
  components: {},
  props: {
    back_text: {
      type: String,
      default: "Back"
    }
  },

  setup(props, {
    emit
  }) {
    const state = reactive({});

    const backClicked = () => {
      emit("backClickedEmit");
    };

    return { ...toRefs(state),
      backClicked
    };
  }

});