import { defineComponent, onMounted, toRefs } from "vue";
import { defaultImgUrl } from "@/const/appConsts";
import { reactive } from "@vue/reactivity";
import BackBtn from "@/components/BackBtn.vue";
import router from "@/router";
import RouterNames from "@/router/name";
import Header from "@/components/HomePage/Header.vue";
export default defineComponent({
  name: "TripPage",
  components: {
    BackBtn,
    Header
  },
  props: {
    id: {
      type: String
    }
  },

  setup(props) {
    const state = reactive({
      tripData: {},
      center: {}
    });

    const getTripData = async () => {
      const activity = await require("@/assets/jsonData/activity.json");
      const tripData = activity.find(_ => {
        return _.actId == props.id;
      });
      state.tripData = tripData;
      state.center = {
        lat: JSON.parse(tripData.longitude),
        lng: JSON.parse(tripData.latitude)
      };
    };

    onMounted(() => {
      getTripData();
    });

    const goBackUrl = () => {
      router.push({
        name: RouterNames.tripList
      });
    };

    return { ...toRefs(state),
      defaultImgUrl,
      goBackUrl
    };
  }

});